.menu-sidebar {
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    background: $color-main;
    z-index: 9999;
    transition: all .25s ease;
    
    &--wrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &--show {
        top: 0;
    }
    
    &__link {
        width: 100%;
        max-width: 190px;
        padding: 6px 0;
        margin: 21px auto 0px auto;   
        text-align: center;
        font-weight: 600;
        color: $color-white;
        text-shadow: 3px 3px 10px $color-black;
        
        &--active,
        &:hover {
            span {
                padding: 0 10px;
                position: relative;
                color: $color-white;

                &:after {
                    content: '';
                    position: absolute;
                    left: 50%;
                    width: 3px;
                    border-radius: 50%;
                    height: 3px;
                    top: 100%;
                    background: $color-cta;
                    transform: translate(-50%, 3px);
                    box-shadow: 9px 0px 0 $color-cta, -9px 0 0 $color-cta;
                }
            }

            &:hover {
                span {    
                    &:after {
                        background: $color-white;
                        box-shadow: 9px 0px 0 $color-white, -9px 0 0 $color-white;
                    }
                }
            }
        }
    }

    &__user-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin: 0 auto;
        width: 100%;
        max-width: 190px;
        margin-bottom: 20px;
    }

    &__bookmarked {
        padding: 8px 12px;
        border: none;
        color: $color-white;

        padding: 0;
        height: 60px;
        width: 60px;
        line-height: 60px;
        text-align: center;

        img {
            width: 60px;
            height: 60px;

            &:hover {
                opacity: .9;
            }
        }

        &--active {
            @include button-style($color-main, $color-cta);
            border: none;

            img {
                border: 3px solid $color-cta;
            }
        }
    }

    &__sign-in-register {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        max-width: 190px;
        margin: 0 auto;

        .menu-sidebar__user {
            margin: 10px 0;
            padding: 9px 0;
            width: 100%;
            text-align: center;
            font-size: 1.5rem;
            font-weight: 700;
        }
    }

    &__user {
        font-size: 3rem;
    }
}

.menu {
    position: fixed;
    top: 0;
    z-index: 99999;    
    width: 100%;
    height: 50px;
    background: $color-main;;
    box-shadow: 5px 5px 15px rgba($color-black, .7);
}
.menu-btn {
    display: inline-block;
    bottom: 26px;
    left: 26px;
    height: 50px;
    width: 50px;
    color: $color-cta;
    cursor: pointer;
    z-index: 99999;

    &__icon {
        position: relative;
        display: block;
        width: 30px;
        height: 2px;
        margin: 50% 0 0 0;
        background: rgba(255, 0, 0, 0);
        border-radius: 2px;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        transition: all .25s ease;
        box-shadow: 11px -1px 0 $color-cta, 22px -1px 0 $color-cta, 32px -1px 0 $color-cta;
        width: 4px;
        height: 4px;
        border-radius: 50%;

        &:after,
        &:before {
            content: '';
            position: absolute;
            left: 9px;
            top: -9px;
            width: 30px;
            height: 3px;
            background: $color-cta;
            border-radius: 2px;
            transition: all .25s ease;            
        }

        &:after {
            top: 8px;
        }
        &__active {
            background: $color-main;
            transition: all .25s ease;
            &:before {
                transform: rotate(45deg);
                top: 0;
                transition: all .25s ease;
            }
            &:after {
                transform: rotate(-45deg);
                top: 0;
                transition: all .25s ease;
            }
            
        }
    }
}


