.pagination {
  position: relative;
  display: flex;
  align-items: baseline;
  z-index: 1;
  
  &.pagination--mobile {
    margin-left: -24px;
    width: 100vw;
    justify-content: space-around;
  }

  &__next {
    margin: 0 24px;
    a:after {
      margin-left: 1rem;
      content: '→'
    }
  }
  &__prev {
    margin: 0 24px;
    a:before {
      margin-right: 1rem;
      content: '←'
    }
  }

}



